
<template>
<div style="height: 100%">
    <el-row>
  <el-col :span="6"><div class="grid-content bg-purple qryitem">
  奖品类别编号：
  <el-input
    placeholder="请输入内容"
    v-model="prizeidInput">
  </el-input>
  </div></el-col>
  <el-col :span="6"><div class="grid-content bg-purple-light qryitem">
    奖品状态：
      <el-select v-model="statusInput" placeholder="请选择">
    <el-option
      v-for="item in statusOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div></el-col>
   <el-col :span="6"><div class="grid-content bg-purple qryitem">
  兑奖人：
  <el-input
    placeholder="请输入内容"
    v-model="buyerIdInput">
  </el-input>
  </div></el-col>
  <el-col :span="4"><div class="grid-content bg-purple-dark">
  <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
  <el-button type="info" icon="el-icon-printer" @click="exportData" >导出</el-button>
  </div>
  </el-col>
</el-row>
<div style="height: 100%">
    <el-table
            v-loading="loading"
            :data="tableData"
             height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  prop="id" label="奖品id"  width="100"></el-table-column>
        <el-table-column prop="prizeName"  label="奖品类别"  width="180"></el-table-column>
        <el-table-column  prop="addressName"  label="兑奖人昵称"  width="180"></el-table-column>
        <el-table-column  prop="address"  label="兑奖人地址" width="180" ></el-table-column>
        <el-table-column  prop="phone"  label="兑奖人联系号码" width="180" ></el-table-column>
        <el-table-column  prop="post_code"  label="邮编" width="180" ></el-table-column>
        <el-table-column  prop="status"  :formatter="formatterStatusColumn" label="奖品状态" width="180" ></el-table-column>
        <el-table-column  operation="操作"  label="操作">
            <template scope="scope">
                   <!-- <el-button  size="small"  @click="download(scope.$index, scope.row)"  icon="el-icon-download" > </el-button> -->
                <el-button v-if="scope.row.status == 2" size="small"  type="success"  @click="prizeLogSend(scope.$index, scope.row)">发货</el-button>
                <el-button  size="small"  @click="showImg(scope.$index, scope.row)">二维码 </el-button>
            </template>
        </el-table-column>
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
<el-dialog
  title="二维码"
  :visible.sync="imgVisible"
  width='370px'>
      <img :src="imgShowPath" class="image" style="width:340px" >
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="closeQr()" >确 定</el-button>
  </span>
</el-dialog>
</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    return {
      form: {
        id: "",
        realName: "",
        viewOrder: "",
        status: ""
      },
      prizeidInput: "",
      statusInput:"",
      buyerIdInput:"",
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      dialogFormVisible: false, //控制添加数据的弹框显示关闭
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      loading: true,
      count: 0,
      currentPage: 1,
      imgShowPath:'',
      imgVisible:false,
      statusData: [],
      imgprefixUrl:'https://www.xingyuanji.com/upload/',
       statusOptions: [{
          value: '1',
          label: '未兑奖'
        }, {
          value: '2',
          label: '已兑奖'
        }, {
          value: '3',
          label: '已发货'
        }, {
          value: '4',
          label: '已过期'
        }],
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    //方法
    closeQr(){
      this.imgShowPath ="";
      this.imgVisible = false;
    },
    initData() {
      var _this = this;

      _this.statusData = [
        {
          id: 1,
          description: "上线"
        },
        {
          id: 2,
          description: "下线"
        }
      ];
    },
    qryData() {
      this.loading = true;
      var that = this;

      var qryId = that.prizeidInput;
      var qryBuyerId = that.buyerIdInput;
      var qryStatus = that.statusInput;
      if(!qryId){
        qryId = null;
      }
     if(! qryBuyerId){
        qryBuyerId = null;
      }
      if(! qryStatus){
        qryStatus = null;
      }
      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
          prizeId: qryId,
          addressName:qryBuyerId,
          status:qryStatus
        }
      };
      console.info(qryData);
      that.$axios.prizeLogQry(qryData).then(
        function(res) {
          //成功之后处理逻辑YeNum;
            console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
          that.loading = false;
        },
        function(res) {
          _this.$message({
                    message: res.msg,
                    type: "error"
                  });
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
        }
      );
    },
    exportData(){
      var qryId = this.prizeidInput;
      var qryStatus = this.statusInput;
     if(! qryId){
        qryId = '';
      }
      if(! qryStatus){
        qryStatus ='';
      }
      var url = 'https://www.xingyuanji.com/wish/prizeLog/export?prizeId='+qryId+'&status='+qryStatus;
        window.open(url);

    },
    formatterStatusColumn(row, column) {
      switch (row.status) {
        case 1:
          return "未兑奖";
          break;
        case 2:
          return "已兑奖";
          break;
          case 3:
          return "已发货";
          break;
          case 4:
          return "已过期";
          break;
        default:
          return "未知";
      }
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    download(index, row){


    },
    showImg(index, row){
      this.imgShowPath = this.imgprefixUrl+row.img;
      this.imgVisible = true;
    },
    prizeLogSend(index, row){
            var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm('此操作将发货, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.prizeLogSend(row.id).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "发货成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }else{
                  _this.$message({
                    message: res.msg,
                    type: "error"
                  });
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "发货失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
    },
    resetForm(formName) {
      var that = this;
      that.$refs[formName].resetFields();
      this.form.id = null;
      this.form.realName = null;
      this.form.status = null;
      this.imageUrl = null;
    }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>  


