
<template>
<div style="height: 100%">
   
    <el-row>
    <el-col :span="6">
      姓名：
    <el-input
        placeholder="请输入内容"
        prefix-icon="el-icon-search"
        v-model="usernameInput">
     </el-input>
    </el-col>
      <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
      <el-button type="success" icon="el-icon-circle-plus-outline" @click="add" >新增</el-button>
  
</el-row>
<div style="height: 100%">
    <el-table
            v-loading="loading"
            :data="tableData"
            height="calc(100% - 110px)"
            style="width: 100%; ">
            <el-table-column  type="index" label="序号"  width="50"></el-table-column>
            <el-table-column prop="name"  label="姓名"  width="180"></el-table-column>
            <el-table-column  prop="username"  label="登录名"  width="180"></el-table-column>
            <el-table-column  prop="sex" :formatter="formatterSexColumn" width="60" label="性别"></el-table-column>
            <el-table-column  prop="phone" label="手机号" width="180" ></el-table-column>
            <el-table-column  prop="roleName"  width="130" label="角色"></el-table-column>
            <el-table-column  operation="操作"  label="操作">
              <template scope="scope">
                  <el-button  size="small"  @click="modify(scope.$index, scope.row)">编辑 </el-button>
                  <el-button  size="small"  type="danger"  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
             </el-table-column>
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="count">
        </el-pagination>
    </div>
</div>
<div>
  <el-dialog title="新增用户" width="500px" top="20px" :visible.sync="dialogFormVisible">
  <el-form :model="form" :rules="rules2"  ref="form">
    <el-form-item label="头像" :label-width="formLabelWidth">
                <el-upload
        class="avatar-uploader"
        action="/wish/file/uploadImg"
        :show-file-list="false"
        :with-credentials ="credentials"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
       <el-form-item label="用户名" :label-width="formLabelWidth">
            <el-input v-model="form.username" auto-complete="off"></el-input>
        </el-form-item>
         <el-form-item label="密码" :label-width="formLabelWidth" prop="password">
            <el-input type="password" v-model="form.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" :label-width="formLabelWidth" prop="checkPass">
            <el-input type="password" v-model="form.checkPass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name" >
            <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth">
    <el-radio-group v-model="form.sex" >
      <el-radio   label="1" >男</el-radio>
      <el-radio   label="0" >女</el-radio>
    </el-radio-group>
    </el-form-item>
    <el-form-item label="手机号" :label-width="formLabelWidth">
            <el-input v-model="form.phone" auto-complete="off"></el-input>
        </el-form-item>
    <el-form-item label="角色" :label-width="formLabelWidth">
         <el-select v-model="form.roleId" placeholder="请选择">
    <el-option
      v-for="item in roleData"
      :key="item.id"
      :label="item.description"
      :value="item.id">
    </el-option>
  </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <!-- <el-button @click="resetForm('form')">重 置</el-button> -->
    <el-button type="primary" @click="submitForm('form')">确 定</el-button>
    
  </div>
</el-dialog>

<el-dialog title="修改用户" width="500px" :visible.sync="dialogUpdateFormVisible">
  <el-form :model="form" :rules="rules2"  ref="modform">
    <el-form-item label="头像" :label-width="formLabelWidth">
                <el-upload
        class="avatar-uploader"
        action="/wish/file/uploadImg"
        :show-file-list="false"
        :with-credentials ="credentials"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
       <el-form-item label="用户名" :label-width="formLabelWidth">
            <el-input v-model="form.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name" >
            <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth">
    <el-radio-group v-model="form.sex" >
      <el-radio   label="1" >男</el-radio>
      <el-radio   label="0" >女</el-radio>
    </el-radio-group>
    </el-form-item>
    <el-form-item label="手机号" :label-width="formLabelWidth">
            <el-input v-model="form.phone" auto-complete="off"></el-input>
        </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogUpdateFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="submitModForm('modform')">确 定</el-button>
  </div>
</el-dialog>
</div>

</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };

    return {
      form: {
        id: "",
        username: "",
        password: "",
        checkpass: "",
        name: "",
        sex: "",
        phone: "",
        roleId: "",
        img:""
      },
      usernameInput: "",
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      dialogFormVisible: false, //控制添加数据的弹框显示关闭
      dialogUpdateFormVisible:false,
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      loading: true,
      credentials:true,
      count: 0,
      currentPage: 1,
      roleData: [],
      imgprefixUrl:'https://139.224.114.192:8080/upload/',
      rules2: {
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
        // name: [
        //     { required: true, message: '请输入名称', trigger: 'blur' },
        //     { min: 1, max: 10, message: '长度在  到 10 个字符', trigger: 'blur' }
        //   ],
      }
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    //方法
    initData() {
      var _this = this;
      _this.$axios.findAllRole().then(
        function(res) {
          //成功之后处理逻辑
          _this.roleData = res.list;
        },
        function(res) {}
      );
    },
    qryData() {
      var that = this;
      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
          name: that.usernameInput
        }
      };
      that.$axios.qryUser(qryData).then(
        function(res) {
          //成功之后处理逻辑
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
          _this.$message({
              message:res.msg,
              type: "warning"
            });
        }
      );
    },
    formatterSexColumn(row, column) {
      switch (row.sex) {
        case 0:
          return "女";
          break;
        case 1:
          return "男";
          break;
        default:
          return "未知";
      }
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    resetForm(formName) {
      var that = this;
      that.$refs[formName].resetFields();
      this.form.id = null;
      this.form.username = null;
      this.form.name = null;
      this.form.password = null;
      this.form.checkpass = null;
      this.form.sex = null;
      this.form.phone = null;
      this.form.roleId = null;
      this.form.img = null;
      this.imageUrl = null;
    },
    submitForm(formName) {
      var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
            _this.$axios.addUser(_this.form).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "添加成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm('form');
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "添加失败",
                  type: "warning"
                });
              }
            );
          
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    submitModForm(formName){
      var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
          return;
          console.info(_this.form);
            _this.$axios.updateUser(_this.form).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "修改成功",
                    type: "success"
                  });
                  _this.dialogUpdateFormVisible = false;
                  _this.resetForm('modform');
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败",
                  type: "warning"
                });
              }
            );
          
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //添加
    add() {
      this.dialogFormVisible = true;
      this.resetForm("form");
    },
    //删除
    handleDelete(index, row) {
      //this.tableData.splice(index, 1);
      var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm('此操作删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.delUser(data).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "删除成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
      

      
    },
    //编辑
    modify(index, row) {
      var that = this;
      that.dialogUpdateFormVisible = true;
      console.info(row);
      that.form.id = row.id;
      that.form.username = row.username;
      that.form.name = row.name;
      that.form.sex = row.sex + "";
      that.form.phone = row.phone;
      that.form.roleId = row.roleId;
      that.form.img = row.img;
      that.imageUrl = that.imgprefixUrl+row.img;
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      if(0 == res.result){
        this.form.img  = res.data;
      }else{
          this.$message.error("上传失败!");
      }
      
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>


