<template>
    <div class="login">
        <div class="login-wrap">
            <img src="@/assets/logo.png"></img>
          <h2>猩愿后台管理系统</h2>
          <div class="login-top">
            <h1>LOGIN</h1>
            <form>
              <input type="text" v-model="name"  placeholder="user Id">
              <input type="password" v-model="password" placeholder="password">
              </form>
              <div class="forgot">
                <a href="#">forgot Password</a>
                <input type="submit" @click="login" value="Login">
              </div>
          </div>
          <div class="login-bottom">
          </div>
        </div>
        <canvas></canvas>
    </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      name: "",
      password: ""
    }
  },
  mounted: function (){
    window.requestAnimationFrame = (function(){
        return  window.requestAnimationFrame       ||
                window.webkitRequestAnimationFrame ||
                window.mozRequestAnimationFrame    ||
                window.oRequestAnimationFrame      ||
                window.msRequestAnimationFrame     ||
                function (callback) { window.setTimeout(callback, 1000 / 60); };
    })();
    this.bgInit();
  },
  methods:{
    login: function(){
		let that = this;
        if(that.name.trim() == ''){
            return that.$message({
                message: "请输入用户名!",
                type: "warning"
            });
        }
        if(that.password.trim() == ''){
            return that.$message({
                message: "请输入密码!",
                type: "warning"
            });
        }
		var logindata = {username:that.name.trim(),password:that.password.trim()};
        this.$axios.subLogin(logindata).then(function (res) {
    		//成功之后处理逻辑
        if (res.result == 0) {
				//that.bus.$emit('loginData',res);
                var str = JSON.stringify(res);
				localStorage.setItem("loginData", str);
                that.$router.push('/');
        }else{
        //that.$router.push('/navi');
            that.$message({
                message: "用户名，密码输入不正确，请重新输入！",
                type: "error"
            });z
        }
    },function (res) {
    })

    },
    bgInit: function(){
      var canvas = document.body.querySelector('canvas'),
            ctx = canvas.getContext('2d'),
            W = canvas.width = window.innerWidth,
            H = canvas.height = window.innerHeight,
            pixels = [];

        for (var x = -400; x < 400; x += 5) {
          for (var z = -250; z < 250; z += 5) {
            pixels.push({x: x, y: 100, z: z});
          }  
        }

        function render(ts) {
          var imageData = ctx.getImageData(0, 0, W, H),
              len = pixels.length,
              fov = 250,
              pixel, scale,
              x2d, y2d, c;

          for (var i = 0; i < len; i++) {
            pixel = pixels[i];
            scale = fov / (fov + pixel.z);
            x2d = pixel.x * scale + W / 2;
            y2d = pixel.y * scale + H / 2;
            if(x2d >= 0 && x2d <= W && y2d >= 0 && y2d <= H) {
              c = (Math.round(y2d) * imageData.width + Math.round(x2d)) * 4;
              imageData.data[c] = 17;
              imageData.data[c + 1] = 122;
              imageData.data[c + 2] = 181;
              imageData.data[c + 3] = 255;
            }
            pixel.z -= 0.4;
            pixel.y = H / 14 + Math.sin(i / len * 15 + (ts / 450)) * 10;
            if (pixel.z < -fov) pixel.z += 2 * fov;
          }
          ctx.putImageData(imageData, 0, 0);
        }

        (function drawFrame(ts){
          requestAnimationFrame(drawFrame, canvas);
          ctx.fillStyle = '#17293a';
          ctx.fillRect(0, 0, W, H);
          render(ts);
        }());
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.login-wrap {
    width: 32%;
    margin: 0 auto;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: -230px;
    margin-left: -304px;
    text-align: center;
}
.login h2 {
    font-size: 30px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    margin: 0px 0px 50px 0px;
    font-family: 'Droid Serif', serif;
}
.login-top {
    background: #e1e1e11a;
    border-radius: 25px 25px 0px 0px;
    -webkit-border-radius: 25px 25px 0px 0px;
    -moz-border-radius: 25px 25px 0px 0px;
    -o-border-radius: 25px 25px 0px 0px;
    padding: 40px 60px;
}
.login-top h1 {
    text-align: center;
    font-size: 27px;
    font-weight: 500;
    color: #F45B4B;
    margin: 0px 0px 20px 0px;
}
.login-top input[type="text"] {
    outline: none;
    font-size: 15px;
    font-weight: 500;
    color: #818181;
    padding: 15px 20px;
    background: #CACACA;
    border: 1px solid #ccc;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -o-border-radius: 25px;
    margin: 0px 0px 12px 0px;
    width: 88%;
    -webkit-appearance: none;
}
.login-top input[type="password"] {
    outline: none;
    font-size: 15px;
    font-weight: 500;
    color: #818181;
    padding: 15px 20px;
    background: #CACACA;
    border: 1px solid #ccc;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -o-border-radius: 25px;
    margin: 0px 0px 12px 0px;
    width: 88%;
    -webkit-appearance: none;
}
.forgot {
    text-align: right;
}
.forgot a {
    font-size: 13px;
    font-weight: 500;
    color: #F45B4B;
    display: inline-block;
    border-right: 2px solid #F45B4B;
    padding: 0px 7px 0px 0px;
}
a {
    text-decoration: none;
}
.forgot input[type="submit"] {
    background: #F45B4B;
    color: #FFF;
    font-size: 17px;
    font-weight: 400;
    padding: 8px 7px;
    width: 20%;
    display: inline-block;
    cursor: pointer;
    border-radius: 6px;
    -webkit-border-radius: 19px;
    -moz-border-radius: 6px;
    -o-border-radius: 6px;
    margin: 0px 7px 0px 3px;
    outline: none;
    border: none;
}
.login-bottom {
    background: #e15748db;
    padding: 30px 65px;
    border-radius: 0px 0px 25px 25px;
    -webkit-border-radius: 0px 0px 25px 25px;
    -moz-border-radius: 0px 0px 25px 25px;
    -o-border-radius: 0px 0px 25px 25px;
    text-align: right;
    border-top: 2px solid #AD4337;
}
canvas{
  height: 100%;
  width: 100%;
}
</style>



