import axios from 'axios'
import qs from 'qs'
import router from '@/router'
// axios 配置
axios.defaults.timeout = 5000;
//axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

axios.defaults.baseURL = '/wish';
//axios.defaults.baseURL = 'https://www.xingyuanji.com/wish';

axios.defaults.withCredentials=true;
//POST传参序列化
axios.interceptors.request.use((config) => {
  if(config.method  === 'post'){
   // config.data = JSON.stringify(config.data);
  }
  return config;
},(error) =>{
  return Promise.reject(error);
});


//返回状态判断
axios.interceptors.response.use((res) =>{
  if(res.data.result == -1){
    router.replace('/login');
    return;
  }
  
  return res;
}, (error) => {
  //404等问题可以在这里处理
  return Promise.reject(error);
});

export function fetch(url, params) {
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      .then(response => {
        resolve(response.data);
      }, err => {
        reject(err);
      })
      .catch((error) => {
        reject(error)
      })
  })
}

export function fetchFrom(url, params) {
  return new Promise((resolve, reject) => {

    // let data = JSON.stringify(params);
    console.info(params);
    axios.post(url,qs.stringify(params), {headers: {"Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"}})
    //axios.post(url, JSON.stringify(params, {headers: {"Content-Type": "application/json"}})
    .then( res => {
          resolve(res.data);  
           })
    .catch(err => {
          reject(err)
        })
  })
}

