
<template>
<div>
   
  <el-row>
  <el-col :span="5"><div class="grid-content bg-purple qryitem">
      机器：
  <el-input
    placeholder="请输入内容"
    v-model="machineId">
  </el-input>
  </div></el-col>
  <el-col :span="8"><div class="grid-content bg-purple-light qryitem">
      时间：
   <el-date-picker
      v-model="timeRange"
      value-format="yyyy-MM-dd"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
  </div></el-col>
  <el-col :span="6"><div class="grid-content bg-purple qryitem">
      订单号：
  <el-input
    placeholder="请输入内容"
    v-model="tradeNo">
  </el-input>
   </div></el-col>
   <el-col :span="2"><div class="grid-content bg-purple">
  <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
   <el-button type="info" icon="el-icon-search" @click="exportSale" >全量导出</el-button>
   </div></el-col>
</el-row>
<div>
  
    <el-table
            v-loading="loading"
            :data="tableData"
            style="width: 100%">
           <el-table-column  type="index"  label="序号"  width="50"></el-table-column>
            <el-table-column prop="id"  label="机器号"  width="180"></el-table-column>
            <el-table-column  prop="name"  label="机器名称"  width="180"></el-table-column>
            <el-table-column  prop="money"  label="销售金额" ></el-table-column>
            <el-table-column  prop="num"  label="销售数量" ></el-table-column>
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    return {
      machineId: "",
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      loading: true,
      count: 0,
      currentPage: 1,
      tradeNo:'',
      machineId:'',
      timeRange:[],
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    //方法
    initData() {
      var _this = this;
    },
    exportSale: function(){
       var url = 'https://www.xingyuanji.com/wish/sale/export?machineId=&status=&startTime=&endTime=';
      window.open(url);
    },
    qryData() {
      var that = this;
      var startTime = that.timeRange[0];
      var endTime  = that.timeRange[1];
      var machineId = that.machineId;
      var tradeNo = tradeNo;
      if(!machineId){
          machineId = null;
      }
      if(!tradeNo){
          tradeNo = null;
      }
      if(!startTime){
          startTime = null;
      }
      if(!endTime){
          endTime = null;
      }

      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition:{
        machineId:machineId,// 机器id
        tradeNo:tradeNo,// 订单号
       // machineName:"小飞机器",// 机器名称
        startTime:startTime,// 最早下单时间
        endTime:endTime// 最迟下单时间
        }
      };
      console.info(qryData);
      that.$axios.saleQry(qryData).then(
        function(res) {
          //成功之后处理逻辑
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
          _this.$message({
              message:res.msg,
              type: "warning"
            });
        }
      );
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>


