                    inputValue: row.name
                }).then(function ({value}) {//编辑保存
                    row.name = value;
                }).catch(function () {  //编辑取消
                    that.$message({
                        type: 'info',
                        message: '取消编辑'
                    });
                });
            }
        }
    }
</script>
<style>
  .el-row {
