
<template>
<div style="height: 100%"> 
   
    <el-row>
  <el-col :span="4"><div class="grid-content bg-purple qryitem">
    上传用户：
  <el-input
    placeholder="请输入内容"
    v-model="nameInput">
  </el-input>
  </div></el-col>
<el-col :span="6"><div class="grid-content bg-purple-light qryitem">
      时间：
   <el-date-picker
      v-model="timeRange"
      value-format="yyyy-MM-dd"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
  </div></el-col>
  <el-col :span="4"><div class="grid-content bg-purple-dark">
  <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
  </div>
  </el-col>
</el-row>
<div style="height: 100%">
  
    <el-table
            v-loading="loading"
            :data="tableData"
             height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  type="index" label="序号"  width="50"></el-table-column>
          <el-table-column   label="图片"  width="250">
             <template scope="scope">
            <img  :src="imgprefixUrl+scope.row.img" class="imageShow" >
             </template>
          </el-table-column>
        <el-table-column  prop="nick_name"  label="上传用户"  width="180"></el-table-column>
        <el-table-column  prop="insert_time"  label="上传时间"  width="180"></el-table-column>
        <el-table-column  prop="status"  :formatter="formatterStatusColumn"  label="审批状态" width="100"  ></el-table-column>
        <el-table-column  operation="操作"  label="操作">
            <template scope="scope">
                <el-button  size="small" v-if="scope.row.status == 1"  @click="approval(scope.$index, scope.row)">审批 </el-button>
            </template>
        </el-table-column>
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>

<el-dialog
  title="审批"
  :visible.sync="approvalVisible"
  width='350px'>
      <img :src="showUrl" class="imageApprove" >
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="approvalPass">通过</el-button>
    <el-button type="danger" @click="approvalNPass">驳回</el-button>
    <el-button @click="approvalVisible = false">取消</el-button>
  </span>
</el-dialog>

</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };

    return {
      form: {
        id: "",
        name: "",
        type: "",
        num: "",
        startTime: "",
        endTime: ""
      },
      nameInput: "",
       timeRange: [],
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      approvalVisible:false,
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      loading: true,
      count: 0,
      currentPage: 1,
      qryStatus:'',
      showUrl:'',
      showApprovalId:'',
       imgprefixUrl:'https://www.xingyuanji.com/upload/',
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    //方法
    initData() {
      var _this = this;
    },
    qryData() {
      var that = this;
       var qryName = that.nameInput;
       if(!that.timeRange){
         that.timeRange=[];
       }
      var startTime = that.timeRange[0];
      var endTime = that.timeRange[1];
      if (!startTime) {
        startTime = null;
      }
      if (!endTime) {
        endTime = null;
      }
      if (!qryName) {
        qryName = null;
      }
      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
          name: qryName, // 上传用户
          startTime: startTime, // 最早时间
          endTime: endTime //最迟时间
        }
      };
      console.info(qryData);
      that.$axios.buyerShowQry(qryData).then(
        function(res) {
          //成功之后处理逻辑
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
          _this.$message({
              message:res.msg,
              type: "warning"
            });
        }
      );
    },
    formatterStatusColumn(row, column) {
      switch (row.status) {
        case 1:
          return "未审核";
          break;
        case 2:
          return "审批通过";
          break;
        case 3:
          return "审核未通过";
          break;
        default:
          return "未知";
      }
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    approval(index, row){
      this.showUrl = this.imgprefixUrl + row.img;
      this.showApprovalId = row.id;
      this.approvalVisible = true;
    },
    approvalPass(){
         var _this = this;
         var data = _this.showApprovalId;
      _this.$confirm('确认提交审核?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.buyerShowPass(data).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "审核通过成功",
                    type: "success"
                  });
                  _this.approvalVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "审核通过失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });

    },
    approvalNPass(){
         var _this = this;
         var data = _this.showApprovalId;
      _this.$confirm('确认提交审核?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.buyerShowNPass(data).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "审驳回成功",
                    type: "success"
                  });
                  _this.approvalVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "审核通过失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });

    }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
 .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .imageShow {
    width: 88px;
    display: block;
  }
  .imageApprove {
    width: 300px;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>


