
<template>
<div style="height: 100%"> 
   
    <el-row>
  <el-col :span="6"><div class="grid-content bg-purple qryitem">
    奖品名称：
  <el-input
    placeholder="请输入内容"
    v-model="nameInput">
  </el-input>
  </div></el-col>
  <!-- <el-col :span="6"><div class="grid-content bg-purple-light qryitem">
    奖品状态：
      <el-select v-model="qryStatus" placeholder="请选择">
    <el-option
      v-for="item in statusOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div></el-col> -->
  <el-col :span="6"><div class="grid-content bg-purple qryitem">
     类别名称：
  <el-input
    placeholder="请输入内容"
    v-model="typeNameInput">
  </el-input>
   </div></el-col>
  <el-col :span="4"><div class="grid-content bg-purple-dark">
  <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
  <el-button type="success" icon="el-icon-circle-plus-outline" @click="add" >新增</el-button>
  </div>
  </el-col>
</el-row>
<div style="height: 100%">
  
    <el-table
            v-loading="loading"
            :data="tableData"
             height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  type="index" label="序号"  width="50"></el-table-column>
          <el-table-column prop="id"  label="奖品类别编号"  width="180"></el-table-column>
        <el-table-column  prop="name"  label="奖品名称"  width="180"></el-table-column>
        <el-table-column  prop="type"  label="类别名称"  width="180"></el-table-column>
        <el-table-column  prop="num"  label="库存"  ></el-table-column>
        <el-table-column  operation="操作"  label="操作">
            <template scope="scope">
                <el-button  size="small"  @click="imgexport(scope.$index, scope.row)">导出小程序码 </el-button>
            </template>
        </el-table-column>
        <!-- <el-table-column  operation="操作"  label="操作">
            <template scope="scope">
                <el-button  size="small"  type="danger"  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                <el-button  size="small"  @click="showQRCode(scope.$index, scope.row)">查看二维码 </el-button>
                <el-button  size="small"  @click="generateQRCode(scope.$index, scope.row)">生成二维码 </el-button>
            </template>
        </el-table-column> -->
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
<el-dialog title="新增奖品" width="500px" top="20px" :visible.sync="dialogFormVisible">
  <el-form :model="form" :rules="rules2"  ref="form">
      <el-form-item label="奖品图片" :label-width="formLabelWidth">
                <el-upload
                class="avatar-uploader"
                action="/wish/file/uploadImg"
                :show-file-list="false"
                swith-credentials ="true"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
       <el-form-item label="奖品名称" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="类别名称" :label-width="formLabelWidth">
            <el-input v-model="form.type" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="库存" :label-width="formLabelWidth">
                    <el-input-number v-model="form.num" :min="0" label="描述文字"></el-input-number>
    </el-form-item>
 <el-form-item label="开始时间" :label-width="formLabelWidth">
                  <el-date-picker
      v-model="form.startTime"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
</el-form-item>
    <el-form-item label="结束时间" :label-width="formLabelWidth">
                  <el-date-picker
      v-model="form.endTime"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
</el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <!-- <el-button @click="resetForm('form')">重 置</el-button> -->
    <el-button type="primary" @click="submitForm('form')">确 定</el-button>
    
  </div>
</el-dialog>


<el-dialog
  title="二维码"
  :visible.sync="qrCodeVisible"
  width='270px'>
      <img src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="qrCodeVisible = false">确 定</el-button>
  </span>
</el-dialog>

</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };

    return {
      form: {
        id: "",
        name: "",
        type: "",
        num: "",
        startTime: "",
        endTime: "",
        img:""
      },
      nameInput: "",
      typeNameInput:"",
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      dialogFormVisible: false, //控制添加数据的弹框显示关闭
      qrCodeVisible:false,
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      imgprefixUrl:'https://139.224.114.192:8080/upload/',
      loading: true,
      count: 0,
      currentPage: 1,
      rules2: {
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      },
      qryStatus:'',
      statusOptions: [{
          value: '1',
          label: '未兑奖'
        }, {
          value: '2',
          label: '已兑奖'
        }, {
          value: '3',
          label: '已发货'
        }, {
          value: '4',
          label: '已过期'
        }],
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    imgexport: function(index, row){
      var url = 'https://www.xingyuanji.com/wish/file/export?path=' + row.zipPath;
      window.open(url);
    },
    handleAvatarSuccess(res, file) {
      
      if(0 == res.result){
        this.form.img  = res.data;
        this.imageUrl = URL.createObjectURL(file.raw);
      }else{
          this.$message.error(res.msg);
      }
      
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    //方法
    initData() {
      var _this = this;
    },
    qryData() {
      var that = this;
       var qryName = that.nameInput;
       var qryType = that.typeNameInput;
      if(!that.nameInput){
        qryName = null;
      }
     if(! that.typeNameInput){
        qryType = null;
      }

      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
           name: qryName
           ,type: qryType
        }
      };
      console.info(qryData);
      that.$axios.prizeQry(qryData).then(
        function(res) {
          //成功之后处理逻辑
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
          _this.$message({
              message:res.msg,
              type: "warning"
            });
        }
      );
    },
    formatterSexColumn(row, column) {
      switch (row.sex) {
        case 0:
          return "女";
          break;
        case 1:
          return "男";
          break;
        default:
          return "未知";
      }
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    resetForm(formName) {
      var that = this;
      that.$refs[formName].resetFields();
      this.form.id = null;
      this.form.name = null;
      this.form.type = null;
      this.form.num = null;
      this.form.startTime = null;
      this.form.endTime = null;
      this.form.img = null;
    },
    submitForm(formName) {
      var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
          //alert('submit!');
          console.info(_this.form.id);
          if (!_this.form.id) {
            _this.$axios.savePrize(_this.form).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "添加成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm('form');
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "添加失败",
                  type: "warning"
                });
              }
            );
          } else {
            _this.$axios.updateUser(_this.form).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "修改成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm('form');
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败,"+JSON.stringify(res),
                  type: "warning"
                });
              }
            );
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //添加
    add() {
      this.dialogFormVisible = true;
      this.resetForm("form");
    },
    //删除
    handleDelete(index, row) {
      //this.tableData.splice(index, 1);
      var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm('此操作删除该奖品, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.delPrize(data).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "删除成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
      

      
    },
    //编辑
    modify(index, row) {
      var that = this;
      that.dialogFormVisible = true;
      console.info(row);
      that.form.id = row.id;
      this.form.name = row.name;;
      this.form.type = row.type;;
      this.form.num = row.num;;
      this.form.startTime = row.startTime;;
      this.form.endTime = row.endTime;;
    },
    generateQRCode(index, row){
            var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm('此操作将生成二维码, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.generateQRCode(row.id).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "生成成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
    },
    showQRCode(index, row){
           var _this = this;
           var data=[row.id];
            _this.$axios.prizeDetail(row.id).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.qrCodeVisible = true;
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "查询失败",
                  type: "warning"
                });
              }
            );
    },
    // handleAvatarSuccess(res, file) {
    //   this.imageUrl = URL.createObjectURL(file.raw);
    // },
    // beforeAvatarUpload(file) {
    //   const isJPG = file.type === "image/jpeg";
    //   const isLt2M = file.size / 1024 / 1024 < 2;

    //   if (!isJPG) {
    //     this.$message.error("上传头像图片只能是 JPG 格式!");
    //   }
    //   if (!isLt2M) {
    //     this.$message.error("上传头像图片大小不能超过 2MB!");
    //   }
    //   return isJPG && isLt2M;
    // }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
 .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>


