import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import navi from '@/components/Navi/Navi.vue'
import userManage from '@/components/userManage.vue'
import sowingMap from '@/components/sowingMap.vue'
import macManage from '@/components/macManage.vue'
import prizeManage from '@/components/prizeManage.vue'
import cashRecord from '@/components/cashRecord.vue'
import saleReport from '@/components/saleReport.vue'
import orderQry from '@/components/orderQry.vue'
import login from '@/components/login.vue'
import testMy from '@/components/testMy.vue'
import buyerShow from '@/components/buyerShow.vue'


Vue.use(Router)
//Vue.prototype.$axios = axios

const router = new Router({
  routes: [
    { path: '/login', name: 'login', component: login, desc: '登录', beforeEnter: (to, from, next) => {
      // ...
        var cookie = getCookie('JSESSIONID');
        if(cookie == null || cookie == ''){
          next();
        }else {
          next('/');
        }
      }
    },
    {
      path: '/',
      name: 'navi',
      component: navi,
      children: [{path: '/userManage',name: 'userManage',component: userManage},
                 {path: '/sowingMap',name: 'sowingMap',component: sowingMap},
                 {path: '/macManage',name: 'macManage',component: macManage},
                 {path: '/prizeManage',name: 'prizeManage',component: prizeManage},
                 {path: '/cashRecord',name: 'cashRecord',component: cashRecord},
                 {path: '/saleReport',name: 'saleReport',component: saleReport},
                 {path: '/orderQry',name: 'orderQry',component: orderQry},
                 {path: '/testMy',name: 'testMy',component: testMy},
                 {path: '/buyerShow',name: 'buyerShow',component: buyerShow}
                ],
      beforeEnter: (to, from, next) => {
        //next();
        var cookie = getCookie('JSESSIONID');
        if(cookie == null || cookie == ''){
          next('/login');
        }else {
          next();
        }
      }
    }
  ]
})

function getCookie(name){
  var strcookie = document.cookie;//获取cookie字符串
  var arrcookie = strcookie.split("; ");//分割
  //遍历匹配
  for ( var i = 0; i < arrcookie.length; i++) {
  var arr = arrcookie[i].split("=");
  if (arr[0] == name){
  return arr[1];
  }
  }
  return "";
}  
export default router;



