<template>
    <div style="height: 100%;">
        <div style="width:100%;background-color: #256cc7; overflow: hidden;height:70px;">
            <img src="@/assets/logo.png" width="40px" height="40px" style="float: left;margin: 15px 0 15px 20px;"></img>
            <span class="demonstration">
                猩愿机后台管理系统
            </span>
            <i class="el-icon-menu" @click="isCollapse = !isCollapse"></i>
            <span class="demonstration" style="float:right;cursor: pointer;">
                <el-dropdown trigger="click" @command="handleCommand" >
                  <span class="el-dropdown-link" style="color:white">
                    admin<i class="el-icon-caret-bottom el-icon--right"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="logout">退出登录</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
            </span>
        </div>

        <div style="height: calc(100% - 70px);">
            <el-menu :default-active="path" class="el-menu-vertical-demo nav"  :class="collapse" :collapse="isCollapse"  @select="select" >
                <el-menu-item v-for = "(item,index) in menudata" :index="item.path" ><i :class="item.classname"></i><span slot="title">{{item.name}}</span></el-menu-item>
            </el-menu>
            <div :class="collapse" class="main-content">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    export default {
        data(){
            let menuInfo = {
                isCollapse: false,
                menudata:[],
            }
            

            return menuInfo;
         
        },
        computed: {
            collapse: function(){
                return {
                    isCollapse: this.isCollapse 
                }
            },
            path: function() {
                return '/' + this.$route.path.split('/').reverse()[0];
             }
        },
        created(){
                var _this=this;
                var data =localStorage.getItem("loginData");
                var loginSuccesInfo = JSON.parse(data);
                let naviInfo = loginSuccesInfo.list;
                console.info(loginSuccesInfo);
                for(var i =0; i < naviInfo.length;i++){
                     if( naviInfo[i].permission== '/user/'){
                        _this.menudata.push({
                            path:"/userManage",
                            name:"用户管理",
                        classname:"el-icon-message"  
                        });   
                    }
                    if( naviInfo[i].permission== '/cycleImg/'){
                        _this.menudata.push({
                            path:"/sowingMap",
                            name:"轮播图管理",
                            classname:"el-icon-picture"
                        });   
                    }
                    if( naviInfo[i].permission== '/machineManager/'){
                        _this.menudata.push({
                             path:"/macManage",
                                name:"机器管理",
                                classname:"el-icon-setting"
                        });   
                    }
                    if( naviInfo[i].permission== '/prize/'){
                        _this.menudata.push({
                             path:"/prizeManage",
                            name:"奖品管理",
                            classname:"el-icon-goods"
                        });  
                         
                    }
                     if( naviInfo[i].permission== '/prizeLog/'){
                        _this.menudata.push({
                            path:"/cashRecord",
                            name:"兑奖记录",
                            classname:"el-icon-time"
                        }); 

                     }
                      if( naviInfo[i].permission== '/sale/'){
                        _this.menudata.push({
                            path:"/saleReport",
                            name:"销售报表",
                            classname:"el-icon-tickets"
                        }); 

                     }
                      if( naviInfo[i].permission== '/order/'){
                        _this.menudata.push({
                            path:"/orderQry",
                            name:"订单查询",
                            classname:"el-icon-search"
                            });

                     }
                    if( naviInfo[i].permission== '/buyerShow/'){
                            _this.menudata.push({
                            path:"/buyerShow",
                            name:"买家秀审核",
                            classname:"el-icon-edit-outline"
                            });

                    }
                     
                      
                }
               
        },
        methods:{
            handleCommand(command){
                    console.info(command);
                    var that = this;
                    if(command == 'logout'){
                          this.$axios.logout().then(function (res) {
                            //成功之后处理逻辑
                                console.info(res);
                                if (res.status == 0) {
                                       _this.$message({
                                        message: "登出失败",
                                        type: "warning"
                                        });
                                }else{
                                 that.$router.push('/login');
                                }
                            },function (res) {
                            })
                            

                    }
            },
            select: function(path){
                this.$router.push(path)
            }
        }
    }
</script>
<style scoped="scoped">
.mianbar{
    height: 40px;
    line-height: 40px;
    line-height: 40px;
    padding: 0 10px;
}
.demonstration{
    float: left;
    color: #d0fbdf;
    margin: 0px 10px;
    line-height: 70px;
    font-size: 24px;
}
.main-content{
    width: calc(100% - 221px);
    height: calc(100% - 20px);
    padding: 10px;
    float: right;
    transition: width .305s linear;
}
 .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
.nav{
    height:100%;
    float: left;
  }
.main-content.isCollapse{
    width: calc(100% - 85px);
}
.el-icon-menu{
    font-size: 35px;
    color: #e6e6ef;
    line-height: 70px;
    cursor: pointer;
}
</style>


