
<template>
<div style="height: 100%"> 
   
    <el-row>
  <el-col :span="24"><div class="grid-content bg-purple-dark">
  名称：
  <el-input
    placeholder="请输入内容"
    prefix-icon="el-icon-search"
    v-model="usernameInput">
  </el-input>
  <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
  <el-button type="success" icon="el-icon-circle-plus-outline" @click="add" >新增</el-button>
  </div>
  </el-col>
</el-row>
<div style="height: 100%">
  
    <el-table
            v-loading="loading"
            :data="tableData"
            height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  type="index" label="序号"  width="50"></el-table-column>
        <el-table-column prop="id"  label="机器编号"  width="180"></el-table-column>
        <el-table-column  prop="name"  label="机器名称"  width="180"></el-table-column>
        <el-table-column  prop="address"  label="机器地址" width="180" ></el-table-column>
        <el-table-column  prop="status" :formatter="formatterStatusColumn"  label="机器状态" width="180" ></el-table-column>
        <el-table-column  operation="操作"  label="操作">
            <template scope="scope">
                <el-button  size="small"  @click="modify(scope.$index, scope.row)">编辑 </el-button>
                <el-button  size="small"  type="danger"  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                <el-button  size="small"  @click="showImg(scope.$index, scope.row)">二维码 </el-button>
                <!-- <el-button  size="small"  type="primary"  @click="handleDelete(scope.$index, scope.row)" plain>更新货道</el-button>
                <el-button  size="small"  type="primary"  @click="handleDelete(scope.$index, scope.row)" plain>更新机器编号</el-button> -->
            </template>
        </el-table-column>
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
<el-dialog title="新增机器" width="500px" top="20px" :visible.sync="dialogSaveFormVisible">
  <el-form :model="form" :rules="rules2"  ref="addform">
     <el-form-item label="机器编号" :label-width="formLabelWidth">
            <el-input v-model="form.id" auto-complete="off"></el-input>
        </el-form-item>
       <el-form-item label="名称" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="代理人" :label-width="formLabelWidth">
            <el-select v-model="form.userId" filterable placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="机器横向商品数" :label-width="formLabelWidth">
                    <el-input-number v-model="form.width" :min="0" label="描述文字"></el-input-number>
    </el-form-item>
    <el-form-item label="机器纵向商品数" :label-width="formLabelWidth">
                    <el-input-number v-model="form.height" :min="0" label="描述文字"></el-input-number>
    </el-form-item>
        <el-form-item label="地址" :label-width="formLabelWidth">
            <el-input v-model="form.address" auto-complete="off"></el-input>
        </el-form-item>
    <el-form-item label="状态" :label-width="formLabelWidth">
         <el-select v-model="form.status" placeholder="请选择">
    <el-option
      v-for="item in statusData"
      :key="item.id"
      :label="item.description"
      :value="item.id">
    </el-option>
  </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogSaveFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="submitForm('addform')">确 定</el-button>
    
  </div>
</el-dialog>
<el-dialog title="修改机器" width="500px" top="20px" :visible.sync="dialogFormVisible">
  <el-form :model="form" :rules="rules2"  ref="form">
     <el-form-item label="机器编号" :label-width="formLabelWidth">
            <el-input v-model="form.id" auto-complete="off" disabled ></el-input>
        </el-form-item>
       <el-form-item label="名称" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="机器横向商品数" :label-width="formLabelWidth">
                    <el-input-number v-model="form.width" :min="0" label="描述文字"></el-input-number>
    </el-form-item>
    <el-form-item label="机器纵向商品数" :label-width="formLabelWidth">
                    <el-input-number v-model="form.height" :min="0" label="描述文字"></el-input-number>
    </el-form-item>
        <el-form-item label="地址" :label-width="formLabelWidth">
            <el-input v-model="form.address" auto-complete="off"></el-input>
        </el-form-item>
    <el-form-item label="状态" :label-width="formLabelWidth">
         <el-select v-model="form.status" placeholder="请选择">
    <el-option
      v-for="item in statusData"
      :key="item.id"
      :label="item.description"
      :value="item.id">
    </el-option>
  </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <!-- <el-button @click="resetForm('form')">重 置</el-button> -->
    <el-button type="primary" @click="editForm('form')">确 定</el-button>
    
  </div>
</el-dialog>

<el-dialog
  title="二维码"
  :visible.sync="imgVisible"
  width='370px'>
      <img :src="imgShowPath" class="image" style="width:340px" >
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="closeQr()" >确 定</el-button>
  </span>
</el-dialog>
</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    

    return {
      form: {
        name: "",
        width:0,
        height:0,
        address: "",
        status:"",
        userId: ""
      },
      usernameInput: "",
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      dialogSaveFormVisible:false,
      dialogFormVisible: false, //控制添加数据的弹框显示关闭
      tableData: [],
      options: [],
      formLabelWidth: "120px",
      imageUrl: "",
      imgVisible:false,
      loading: true,
      count: 0,
      currentPage: 1,
      imgShowPath:"",
      statusData: [],
      imgprefixUrl:'https://www.xingyuanji.com/upload/',
      rules2: {
       // password: [{ validator: validatePass, trigger: "blur" }],
       // checkPass: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  mounted() {
    var that =this;
    this.$axios.getAgent().then(function(res){
      if(res.result == 0){
        that.options = res.list
      }
    })
  },
  methods: {
    //方法
    initData() {
      var _this = this;
      _this.statusData = [
        {
          id: 1,
          description: "正常"
        },
        {
          id: 2,
          description: "损坏"
        }
      ];
    },
    qryData() {
      var that = this;
      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
          name: that.usernameInput
        }
      };
      that.$axios.macQry(qryData).then(
        function(res) {
          //成功之后处理逻辑
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
          _this.$message({
                    message: res.msg,
                    type: "error"
                  });
        }
      );
    },
    formatterStatusColumn(row, column) {
      switch (row.status) {
        case 1:
          return "正常";
          break;
        case 2:
          return "损坏";
          break;
        default:
          return "未知";
      }
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    resetForm(formName) {
      var that = this;
      that.$refs[formName].resetFields();
      this.form.id = null;
      this.form.name = null;
      this.form.address = null;
      this.form.status = null;
      this.form.width = null;
      this.form.height = null;
      this.form.userId = null;
    },
    submitForm(formName) {
      var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
            _this.$axios.saveMac(_this.form).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "添加成功",
                    type: "success"
                  });
                  _this.dialogSaveFormVisible = false;
                  _this.resetForm('addform');
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "添加失败"+res.msg,
                  type: "warning"
                });
              }
            );
          
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    editForm(formName){
        var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
            _this.$axios.updateMac(_this.form).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "修改成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm('form');
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败"+res.msg,
                  type: "warning"
                });
              }
            );
          
        } else {
          console.log("error submit!!");
          return false;
        }
      });

    },
    //添加
    add() {
      this.dialogSaveFormVisible = true;
      this.resetForm("form");
    },
    //删除
    handleDelete(index, row) {
      //this.tableData.splice(index, 1);
      var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm('此操作删除该机器, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         _this.$axios.deletesMac(data).then(
              function(res) {
                //成功之后处理逻辑
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "删除成功",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //失败之后处理逻辑
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "修改失败",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
      

      
    },
    //编辑
    modify(index, row) {
      var that = this;
      that.dialogFormVisible = true;
      console.info(row);
      that.form.id = row.id;
      this.form.name = row.name;
      this.form.address = row.address;
      this.form.width = row.width;
      this.form.height = row.height;
      this.form.status = row.status;
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    showImg(index, row){
      this.imgShowPath = this.imgprefixUrl+row.qrcode;
      this.imgVisible = true;
    },
    closeQr(){
      this.imgShowPath ="";
      this.imgVisible = false;
    }
  }

};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>


