
<template>
<div style="height: 100%">
   
  <el-row>
  <el-col :span="5"><div class="grid-content bg-purple qryitem">
      订单号：
  <el-input
    placeholder="请输入内容"
    v-model="payTradeNo">
  </el-input>
  </div></el-col>
<el-col :span="5"><div class="grid-content bg-purple qryitem">
      下单人：
  <el-input
    placeholder="请输入内容"
    v-model="buyerName">
  </el-input>
  </div></el-col>
<el-col :span="5">
  <div class="grid-content bg-purple-light qryitem">
    订单状态：
      <el-select v-model="payStatus" placeholder="请选择">
    <el-option
      v-for="item in statusOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div>
  </el-col>
  <el-col :span="7"><div class="grid-content bg-purple-light qryitem">
      时间：
   <el-date-picker
      v-model="timeRange"
      value-format="yyyy-MM-dd"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
  </div></el-col>
    <el-col :span="2"><div class="grid-content bg-purple">
  <el-button type="primary" icon="el-icon-search" @click="qryData" >搜索</el-button>
   </div></el-col>
</el-row>
<div style="height: 100%">
  
    <el-table
            v-loading="loading"
            :data="tableData"
            height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  type="index"  label="序号"  width="50"></el-table-column>
        <el-table-column prop="tradeNo"  label="订单号"  width="280"></el-table-column>
        <el-table-column  prop="nickName"  label="下单人"  width="180"></el-table-column>
        <el-table-column  prop="insertTime"  label="下单时间" width="180" ></el-table-column>
        <el-table-column  prop="status"  :formatter="formatterStatusColumn" label="订单状态"  ></el-table-column>
        <!-- <el-table-column  prop="mac"  label="下单机器"  ></el-table-column> -->
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    return {
      payTradeNo: "",
      buyerName: "",
      payStatus: "",
      timeRange: [],
      formLabelWidth: "50px", //添加数据弹框label标签宽度
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      loading: true,
      count: 0,
      currentPage: 1,
      statusOptions: [
        {
          value: "0",
          label: "未支付"
        },
        {
          value: "1",
          label: "支付成功"
        }
      ]
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    //方法
    initData() {
      var _this = this;
    },
    qryData() {
      var that = this;
      var startTime = that.timeRange[0];
      var endTime = that.timeRange[1];
      var payTradeNo = that.payTradeNo;
      var buyerName = that.buyerName;
      var payStatus = that.payStatus;
      if (!startTime) {
        startTime = null;
      }
      if (!endTime) {
        endTime = null;
      }

      if (!payTradeNo) {
        payTradeNo = null;
      }
      if (!buyerName) {
        buyerName = null;
      }
      if (!payStatus) {
        payStatus = null;
      }
      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
          payTradeNo: payTradeNo, // 订单号
          buyerName: buyerName, // 购买人昵称
          payStatus: payStatus, // 支付状态，0未支付，1支付成功
          startTime: startTime, // 最早下单时间
          endTime: endTime //最迟下单时间
        }
      };
      console.info(qryData);
      that.$axios.orderQry(qryData).then(
        function(res) {
          //成功之后处理逻辑
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //失败之后处理逻辑
          console.info("error");
          console.info(res);
          _this.$message({
            message: res.msg,
            type: "warning"
          });
        }
      );
    },
    handleSizeChange(val) {
      //console.log(`每页 ${val} 条`);
    },
    formatterStatusColumn(row, column) {
      switch (row.status) {
        case 0:
          return "未支付";
          break;
        case 1:
          return "支付成功";
          break;
        default:
          return "未知";
      }
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>


